<template>
  <div id="app">
    <div class="container-fluid" style="padding: unset">
      <Header v-if="noticeIs !== ''" img="" :noticeIs="noticeIs" school='Go语言实战抽奖系统'></Header>
      <div class="row top-main">
        <div class="col-md-8 col-md-offset-2 mt-2">
          <i class="iconfont iconxuexirenshu" style="color: white"></i><span style="color: white;font-size:18px;font-family:PingFang-SC-Medium;font-weight:500;margin-left: 10px">助教中心</span>
        </div>
      </div>
      <div class="row" style="background:rgba(255,255,255,1);box-shadow:0px 3px 15px 0px rgba(0, 0, 0, 0.08);height: 59px;line-height: 59px">
        <div class="col-md-1 col-md-offset-2" @click="changePractical('school')" style="cursor: pointer">
          <span class="col-sm-12 text-center font-tab" :class="practicalSelect == 'school'?'active-font-color':''">校园实训</span>
          <span class="col-sm-4 col-sm-offset-4 text-center" :class="practicalSelect == 'school'?'active-bg-color':''" style="height:2px;border-radius:1px;"></span>
        </div>
        <div class="col-md-1" @click="changePractical('company')" style="cursor: pointer">
          <span class="col-sm-12 text-center font-tab" :class="practicalSelect == 'company' ?'active-font-color':''" >企业实训</span>
          <span class="col-sm-4 col-sm-offset-4 text-center" :class="practicalSelect == 'company' ?'active-bg-color':''" style="height:2px;border-radius:1px;"></span>
        </div>
      </div>
      <div class="row pt-4 mt-3">
        <div class="col-md-6 col-md-offset-2 ">
          <div class="col-md-12 pl-0 pr-0 project-info mt-2 mb-3" v-for="item in projectList" v-bind:key="item.facultyId" :class="[item.isfocus?'active-tip':'unactive-tip']" @mouseover="changeFocusStatus(item)" @mouseout="changeFocusStatus(item)">
          <div class="col-md-3 text-center">
            <img :src="item.logoPath" style="margin-top:28px;width: 90px;height: 90px;border-radius: 50%" >
          </div>
          <div class="col-md-9" @click="gotoProjectList(item.schoolId, item.facultyId)">
            <span class="col-sm-12" style="height: 29px;height:18px;font-size:18px;font-family:PingFang-SC-Bold;font-weight:bold;color:rgba(51,51,51,1);margin-top:28px;">{{item.schoolName}}/{{item.facultyName}}</span>
            <span v-if="item.overtimeTask !== 0" class="tip text-center" style="border: 1px solid #FF4C55;border-radius: 10px;"><i class="iconfont icongantanhao" style="color: #FF4C55"></i><span style="color: #FF4C55;font-size: 12px;margin-left: 8px;">即将超时任务：{{item.overtimeTask}}项</span></span>
            <span class="col-sm-2 text-left practical-number">{{item.studentNum}}</span>
            <span class="col-sm-2 text-left practical-number">{{item.projectNum}}</span>
            <span class="col-sm-2 text-left practical-number">{{item.noncommentNum}}</span>
            <span class="col-sm-6 text-left practical-number">{{item.answerquestionNum}}</span>
            <span class="col-sm-2 text-left practical-name">学生数</span>
            <span class="col-sm-2 text-left practical-name">项目数</span>
            <span class="col-sm-2 text-left practical-name">待点评</span>
            <span class="col-sm-6 text-left practical-name" style="border: none;">Q&A</span>
          </div>
          </div>
        </div>
        <div class="col-md-2 assistant-info ml-4 pl-0 pr-0">
          <div class="ass-set"><a href="assistant-set.html">个人设置</a></div>
          <div class="col-md-12  pl-0 pr-0 text-center">
            <img :src="assistantImg" style="width: 91px;height: 91px;border-radius: 50%;margin-top:27px"></div>
            <span class="col-md-12 text-center mt-4" >{{assistantName}}</span>
          <span class="col-md-4 col-md-offset-4 text-center mt-4" style="height:17px;line-height:17px;background:rgba(255,150,0,1);border-radius:9px;"><i class="iconfont iconjinpai" style="color: yellow;float: left;margin-left: -8px;"></i><span style="font-size: 6px;font-family: PingFang-SC-Medium;font-weight: 500;color: rgba(255,255,255,1);">金牌助教</span></span>
          <span class="col-md-10 col-md-offset-1 text-center mt-4" style="font-size:14px;font-family:PingFang-SC-Medium;font-weight:500;color:rgba(102,102,102,1);">累计积分：3950</span>
          <span class="col-md-12 text-center mt-5 practical-name" style="border: none">截止上次登录时间：2019-02-08  09:09:16</span>
          <span class="col-md-12 mt-3" style="height:1px;background:rgba(235,235,235,1);"></span>
          <div class="col-md-12 mt-2">
            <div class="col-md-4 mt-3">
              <span class=" practical-name col-md-12 pr-0 " style="border: none;">项目点评</span>
              <span class=" ass-number col-md-12 text-center">{{commentProjectNum}}</span>
            </div>
          <div class="col-md-4 mt-3">
            <span class=" practical-name col-md-12 pr-0 " style="border: none;">答疑人数</span>
            <span class=" ass-number col-md-12 text-center">{{answerNum}}</span>
          </div>
          <div class="col-md-4 mt-3">
            <span class=" practical-name col-md-12 pr-0 "  style="border: none;">任务点评</span>
            <span class=" ass-number col-md-12 text-center">{{commentMissonNum}}</span>
          </div>
          </div>
        </div>
      </div>
      </div>
    </div>
</template>

<script>
import Header from '../../../components/assistant/Header.vue'
export default {
  name: 'app',
  components: {
    Header
  },
  mounted () {
    this.loadData()
  },
  data () {
    return {
      assistantName: '', // 助教名
      commentProjectNum: '', // 点评项目数
      assistantImg: '../../../assets/img/assistant_bg.png',
      answerNum: '', // 答疑人数
      commentMissonNum: '', // 点评任务数
      practicalSelect: 'school',
      projectList: [{ isfocus: false }
      ],
      noticeIs: ''
    }
  },
  methods: {
    loadData () {
      let me = this
      if (localStorage.getItem('unreadStatus') === 'false') {
        me.noticeIs = false
      } else {
        me.noticeIs = true
      }
      me.$http.post('/assistant/firstpage', { 'token': localStorage.getItem('token') }, res => {
        if (res.success) {
          // console.log(res.data)
          for (var i = 0; i < res.data.initproject.length; i++) {
            res.data.initproject[i].isfocus = false
          }
          me.projectList = res.data.initproject
          me.assistantName = res.data.initmsg.assistantName
          me.commentProjectNum = res.data.initmsg.commentProjectNum
          me.answerNum = res.data.initmsg.answerNum
          me.commentMissonNum = res.data.initmsg.commentMissionNum
          me.assistantImg = res.data.initmsg.assistantNamePhotoUrl
        }
      })
    },
    changePractical  (practical) {
      if (practical === 'company') {
        this.$alert('暂未开放！')
        return false
      }
      this.practicalSelect = practical
    },
    gotoProjectList (schoolId, facultyId) {
      window.location = 'project-list.html?schoolId=' + schoolId + '&facultId=' + facultyId
    },
    changeFocusStatus (obj) {
      obj.isfocus = !obj.isfocus
    },
    personalSet () {
      window.location = 'assistant-set.html'
    }
  }
}
</script>
<style scoped lang="scss">
  @import "../../../assets/css/bootstrap.css";
  @import "../../../assets/css/theme.css";
  @import "../../../assets/css/text.css";
  .top-main{
    background-image: url(../../../assets/img/assistant_bg.png);
    background-size: 100% 100%;
    height: 138px;
  }
  .font-tab{
    font-size:16px;
    font-family:PingFang-SC-Bold;
    font-weight:bold;
  }
  .active-font-color{
    color: #6612CF;
  }
  .active-bg-color{
    background-color: #6612CF;
  }
  .project-info{
    height:146px;
    background:rgba(255,255,255,1);
    border-radius: 10px;
    padding: 0px;
  }
  .active-tip{
    box-shadow:0px 3px 15px 0px rgba(0, 0, 0, 0.2);
  }
  .unactive-tip{
    box-shadow:0px 3px 15px 0px rgba(0, 0, 0, 0.08);
  }
  .assistant-info{
    min-height:348px;
    background:rgba(255,255,255,1);
    box-shadow:0px 2px 15px 0px rgba(0, 0, 0, 0.08);
    border-radius:10px;
    .ass-set{
      font-size: 12px;
      z-index: 1001;
      font-weight: bold;
      position: absolute;
      color: #6612cf;
      right: 0;
      margin-right: 5%;
      margin-top: 5%;
      display: none;
      a {
        cursor: pointer;
        color: #6612cf;
      }
      a:hover{
        cursor: pointer;
        text-decoration: underline;
        color: #6612cf;
      }
    }
  }
  .assistant-info:hover{
    background:rgba(255,255,255,1);
    box-shadow:0px 3px 15px 0px rgba(0, 0, 0, 0.2);
    .ass-set{
      z-index: 1001;
      display: block;
    }
  }
  .practical-number{
    height:18px;
    font-size:24px;
    font-family:PingFang-SC-Bold;
    font-weight:bold;
    color:rgba(102,102,102,1);
    margin-top:20px;
  }
  .ass-number{
    height:18px;
    font-size:24px;
    font-family:PingFang-SC-Bold;
    font-weight:bold;
    color:rgba(102,102,102,1);
    margin-top:5px;
  }
  .practical-name{
    height:12px;
    font-size:10px;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(102,102,102,1);
    margin-top:20px;
    border-right: 1px solid #999999;
  }
  .tip{
    position: absolute;
    top: 0;
    right: 0;
    width: 159px;
    margin-top: 95px;
    margin-right: 24px;
  }
</style>
<style>
  .el-tabs__item:hover, .el-tabs__item.is-active {
    color: #6612CF!important;
    cursor: pointer;
  }
  .el-tabs__active-bar{
    background-color: #6612CF!important;
  }
</style>
